import { Routes, RouterModule }     from '@angular/router'

import { PostsComponent }           from './posts.component'
import { PostEditComponent }        from './post-edit/post-edit.component'
import { PostStartComponent }       from './post-start/posts-start.component'
import { PostDetailsComponent }     from './post-details/post-details.component'
import { AuthGuard }                from "app/shared/auth.guard";

const POSTS_ROUTES : Routes=[
    {path:'',component:PostsComponent, children:[
        {path:'',component:PostStartComponent},
        {path:'post',component:PostEditComponent},
        {path:'details/:id',component:PostDetailsComponent},
        {path:':id/edit',component:PostEditComponent,canActivate: [AuthGuard]}
        ]}
];

export const postsRouting = RouterModule.forChild(POSTS_ROUTES);


