import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router'
import { Subscription } from 'rxjs/Rx'
import { FormBuilder, 
  FormArray, 
  FormGroup, 
  FormControl,
  Validators }  from '@angular/forms'

import { LoginUser } from "app/shared/loginUser";
import { PostDetails } from '../post'
import { UpperCaseDirective } from 'app/shared/upper-case.directive'
import { FirebaseMOSService } from 'app/shared/firebaseMOS.service'

@Component({
  selector: 'mos-post-edit',
  template: require('./post-edit.component.html')
  
})
export class PostEditComponent implements OnInit, OnDestroy {
  loginUser:LoginUser;
  postForm : FormGroup;
  private subscription: Subscription;
  private postId : string;
  selectedPost=new PostDetails();
  title:String;
  isNew=true;
  isLoaded=false;

  constructor(private route:ActivatedRoute,
              private fb:FormBuilder,
              private router:Router,
              private srv:FirebaseMOSService) { }

  ngOnInit() {
    this.isNew=true;
    
    this.loginUser= this.srv.loginUser;

    if(this.route.snapshot.params.hasOwnProperty('id')){
      this.isNew=false;
      this.postId = this.route.snapshot.params['id'];
      this.loadPost();
    } 
    this.postForm = this.fb.group({
            postDescription:['',Validators.required],
            publishFrom:['',Validators.required],
            publishTill:['',Validators.required],
            locationZipCode:['',Validators.required],
            locationCity:['',Validators.required],
            locationState:['',Validators.required],
            locationAddress:[''],
            contactNumber:[''],
            petFree:[false,Validators.required],
            smokeFree:[false,Validators.required],
            pickupFree:[false,Validators.required],
            additionalNotes:[''],
            externalImagesLink:['']
        });
    // this.initForm();
  }

  loadCityState(zipcode){
    console.log(zipcode);
    this.srv.getCityState(zipcode)
      .subscribe(res=>{
        console.log(res);
        if(res){
          this.postForm.controls['locationCity'].setValue( res.city);
          this.postForm.controls['locationState'].setValue( res.state);
        }else
        {
          alert("Please enter valid zip code")
        }
      });
  }


  loadPost(){
    console.log("Get Post");
    this.srv.getPost(this.postId);
      // .subscribe(res=>{ this.selectedPost=res;
      //                   this.initForm();
      //                 },
      //             error=>console.log(error));
  }

  ngOnDestroy(){
    // this.subscription.unsubscribe;
  }

  onSubmit(){
    if(!this.postForm.valid)
    {
      alert("Form has errors.");
      return;
    }
    var currentPost:PostDetails = this.postForm.value;
    if(this.isNew){
      console.log(this.loginUser);
      if (this.loginUser==null){
        alert("Please login to post");
        return;
      }
      currentPost = this.applyPostDetailsPost(currentPost);
      
      this.srv.addPost(currentPost)
        .then(res=>{ alert("New post added");
                    this.postId = res.key; 
                    console.log(res);
                    console.log(res.key);
                    this.navigateToPost();},
              error=>{console.log(error);
                      alert("Some error occured: "+error)});
          

    }else{
      console.log("Update Post");
      // newRecipe.key= this.recipeId;
      // this.srv.updateRecipe(newRecipe)
      //   .subscribe(res=>console.log(res),
      //             error=>console.log(error),
      //             ()=>this.srv.onChanges.emit(true));;
    }
  }

  onCancel(){
    this.navigateBack();
  }

  private navigateBack(){
    this.router.navigate(["/posts"]);
  }

  private navigateToPost(){
    this.router.navigate(["/posts/details/"+this.postId]);
  }

  private initForm(){
    console.log("initForm");
    console.log(this.selectedPost);
    let recipeName = "";
    let recipeDescription = "";
    let recipeImagePath = "";
    let recipeIngredients:FormArray = new FormArray([]);
    this.title = (this.isNew)?"New ":"Edit";
    
    if(!this.isNew){
      // recipeName = this.selectedRecipe.name;
      // recipeImagePath = this.selectedRecipe.imagePath;
      // recipeDescription = this.selectedRecipe.description;
      
      //add ingredients
      // if (this.selectedRecipe.ingredients!=null)
      // {
      //   this.selectedRecipe.ingredients.forEach(element => {
      //     recipeIngredients.push(
      //       new FormGroup({
      //         name:new FormControl(element.name,Validators.required),
      //         amount:new FormControl(element.amount,
      //                               [ Validators.required,
      //                                 Validators.pattern("\\d+")
      //                               ])
      //       })
      //     );        
      //   });
      // }
    }

    //build the forms
    this.postForm = this.fb.group({
      name:[recipeName,Validators.required],
      imagePath:[recipeImagePath,Validators.required],
      description:[recipeDescription,Validators.required],
      ingredients:recipeIngredients
    })
  }

  private applyPostDetailsPost (currentPost):PostDetails {
    var currentTime = new Date().toLocaleString();
    var updatedPost = new PostDetails();
    updatedPost.additionalNotes = currentPost.additionalNotes;
    updatedPost.contactNumber   = currentPost.contactNumber;
    updatedPost.createdOn       = currentTime;
    updatedPost.externalImagesLink  = currentPost.externalImagesLink;
    updatedPost.location        = currentPost.locationCity+","
                                  +currentPost.locationState;
    updatedPost.locationAddress = currentPost.locationAddress;
    updatedPost.locationCity    = currentPost.locationCity;
    updatedPost.locationState   = currentPost.locationState;
    updatedPost.locationZipCode = currentPost.locationZipCode;
    updatedPost.petFree         = currentPost.petFree;
    updatedPost.pickupFree      = currentPost.pickupFree;
    updatedPost.postDescription = currentPost.postDescription;
    updatedPost.publishFrom     = currentPost.publishFrom;
    updatedPost.publishTill     = currentPost.publishTill;
    updatedPost.smokeFree       = currentPost.smokeFree;
    updatedPost.updatedOn       = currentTime;
    updatedPost.userUID         = this.loginUser.userUID;

    return updatedPost;
  }
}




