import { Component, OnInit }  from '@angular/core';
import { Router }             from '@angular/router'

import { AuthService }        from 'app/shared/auth.service'
import { FirebaseMOSService } from 'app/shared/firebaseMOS.service'
import { PostDetails }        from '../post'
import { LoginUser }          from "app/shared/loginUser";
import { SpinnerComponent }   from 'app/shared/spinner.component'

@Component({
  selector: 'mos-post-start',
  template: require('./post-start.component.html'),
  styles:[require('./post-start.component.css')]
})
export class PostStartComponent implements OnInit {
  posts:PostDetails[]=[];
  fbPosts:PostDetails[]=[];
  selectedPost:PostDetails;
  loginUser:LoginUser;
  isLoading:boolean;

  constructor(private srv:FirebaseMOSService,
              private authSrv:AuthService,
              private router:Router) {
    this.isLoading=true; 
    this.authSrv.loginChange
        .subscribe(u=>{this.loginUser=u;});
    this.authSrv.updateSigninUser();

  }

  ngOnInit() {
    this.loadPosts();
    console.log(this.loginUser);
  }

  loadPosts(){
    if(this.loginUser==null){
      alert("Please login");
      return;
    }
    console.log("Loading posts for user "+ this.loginUser.email);
    // this.srv.getUserPosts(this.loginUser.userUID)
    //   .subscribe(res=>{console.log(res);
    //                     this.filterPosts(res);
    //                     this.loading=false;
    //     });
    this.srv.getFBUserPosts(this.loginUser.userUID)
      .subscribe(res=>{this.posts=res;
        this.isLoading=false;});
  }

  onCreateNew(){
    this.router.navigate(['posts/post']);
  }

  private filterPosts(allPosts:PostDetails[]){
    this.posts = [];
    console.log("Before Filter");
    console.log(allPosts.length);
    this.posts = allPosts.filter(a=>a.userUID==this.loginUser.userUID);
    console.log("After Filter");
    console.log(this.posts.length);
  }

}



